.exibitions-wrapper{
   display: flex;
   max-width: 1280px;
   width: 100%;
   height: 985px;
   justify-content: center;
   @media(max-width: 768px){
      max-width: 768px;
      width: 100%;
      height: 964px;
   }
   @media(max-width: 320px){
      max-width: 320px;
      width: 100%;
      height: 1667px;
   }
   .exibitions-container{
      display: flex;
      max-width: 1170px;
      width: 100%;
      margin-top: 110px;
      flex-direction: column;
      justify-content: space-between;
      height: 875px;
      @media(max-width: 768px){
         max-width: 690px;
         width: 100%;
         height: 864px;
         margin-top: 100px;
      }
      @media(max-width: 320px){
         max-width: 280px;
         width: 100%;
         height: 1577px;
         margin-top: 90px;
      }
      .exibitions-title-container{
         display: flex;
         width: 100%;
         height: 60px;
         justify-content: space-between;
         align-items: center;
         @media(max-width: 320px){
            justify-content: center;
         }
         .title{
            color: #1C1B29;
            font-size: 48px;
            font-family: Playfair Display;
            font-weight: 700;
            line-height: 57.60px;
            word-wrap: break-word;
            @media(max-width: 768px){
               font-size: 40px;
               line-height: 50px;
            }
            @media(max-width: 320px){
               font-size: 28px;
               line-height: 35px;
            }
         }
         .archive-button{
            display: flex;
            max-width: 270px;
            width: 100%;
            height: 60px;
            border: 1px solid #CD4D31;
            justify-content: center;
            align-items: center;
            color: #CD4D31;
            font-size: 16px;
            font-family: Raleway;
            font-weight: 600;
            line-height: 16px;
            word-wrap: break-word;
            @media(max-width: 768px){
               max-width: 210px;
               width: 100%;
            }
            @media(max-width: 320px){
               display: none;
            }
            &:hover{
               cursor: pointer;
            }
            img{
               width: 20px;
               height: 20px;
               margin-left: 8px;
            }
         }
      }
      .exibitions-items{
         display: flex;
         width: 100%;
         height: 745px;
         justify-content: space-between;
         @media(max-width: 768px){
            height: 734px;
         }
         @media(max-width: 320px){
            flex-direction: column;
            height: 1392px;
         }
         .exibition-item{
            display: flex;
            max-width: 570px;
            width: 100%;
            height: 100%;
            flex-direction: column;
            @media(max-width: 768px){
               max-width: 330px;
               width: 100%;
            }
            @media(max-width: 320px){
               max-width: 280px;
               width: 100%;
               height: 666px;
            }
            .exibition-picture{
               display: flex;
               width: 100%;
               height: 484px;
               @media(max-width: 768px){
                  height: 420px;
               }
               @media(max-width: 320px){
                  height: 356px;
               }
               img{
                  width: 100%;
                  height: 100%;
                  object-fit: cover;
               }
            }
            .exibition-date{
               display: flex;
               width: 100%;
               height: 24px;
               margin-top: 20px;
               color: #CD4D31;
               font-size: 16px;
               font-family: Raleway;
               font-weight: 400;
               line-height: 24px;
               word-wrap: break-word;
               @media(max-width: 768px){
                  margin-top: 30px;
               }
               @media(max-width: 320px){
                  margin-top: 20px;
               }
            }
            .exibition-title{
               display: flex;
               width: 100%;
               height: 30px;
               margin-top: 10px;
               color: #1C1B29;
               font-size: 24px;
               font-family: Playfair Display;
               font-weight: 700;
               line-height: 30px;
               word-wrap: break-word;
               @media(max-width: 768px){
                  margin-top: 10px;
                  height: 60px;
               }
               @media(max-width: 320px){
                  margin-top: 10px;
                  height: 50px;
               }
            }
            .exibition-description{
               display: flex;
               width: 90%;
               height: 48px;
               margin-top: 20px;
               color: #504F5E;
               font-size: 16px;
               font-family: Raleway;
               font-weight: 400;
               line-height: 24px;
               word-wrap: break-word;
               @media(max-width: 768px){
                  height: 70px;
               }
               @media(max-width: 320px){
                  height: 96px;
               }
            }
            .buy-tickets-button{
               display: flex;
               width: 270px;
               height: 60px;
               justify-content: center;
               align-items: center;
               background-color: #CD4D31;
               margin-top: 50px;
               color: #F1F5F4;
               font-size: 16px;
               font-family: Raleway;
               font-weight: 600;
               line-height: 16px;
               word-wrap: break-word;
               @media(max-width: 768px){
                  margin-top: 40px;
                  width: 100%;
               }
               @media(max-width: 320px){
                  margin-top: 30px;
               }
               &:hover{
                  cursor: pointer;
               }
            }
         }
      }
      .archive-button-under{
         display: none;
         width: 100%;
         height: 60px;
         border: 1px solid #CD4D31;
         justify-content: center;
         align-items: center;
         color: #CD4D31;
         font-size: 16px;
         font-family: Raleway;
         font-weight: 600;
         line-height: 16px;
         word-wrap: break-word;
         @media(max-width: 320px){
            display: flex;
         }
         &:hover{
            cursor: pointer;
         }
         img{
            width: 20px;
            height: 20px;
            margin-left: 8px;
         }
      }
   }
}